import React ,{Component} from "react";
import{Row, Col, Form, Input, Label, FormFeedback, Button} from 'reactstrap'

class Formulario extends Component
{
    constructor(props)
    {
        super(props)

        this.state =
        {
            Nombre: "Homero",
            Apellido: "",
            Fecha:"",
            correo: "",
            confirmacionCorreo: "",
            clave: "",
            mensajeNombre:"",
            mensajeApellido:"",
            mensajeFecha:"",
            mensajeCorreo:"",
            mensajeConfirmacion:"",
            mensajeClave: "",
            invalidNombre: false,
            invalidApellido: false,
            invalidFecha: false,
            invalidCorreo: false,
            invalidConfirmacionCorreo: false,
            invalidClave: false,
            
        }
        this.cambiarNombre = this.cambiarNombre.bind(this);
        this.cambiarApellido = this.cambiarApellido.bind(this);
        this.cambiarFecha = this.cambiarFecha.bind(this);
        this.cambiarCorreo = this.cambiarCorreo.bind(this);
        this.confirmacionCorreo = this.confirmacionCorreo.bind(this);
        this.cambiarclave = this.cambiarclave.bind(this);
        this.enviar = this.enviar.bind(this);

    }
    
    cambiarNombre(e)
    {
        this.setState({
            Nombre: e.target.value
        })
    }
    cambiarApellido(e)
    {
        this.setState({
            Apellido: e.target.value
        })
    }
    cambiarFecha(e)
    {
        this.setState({
            Fecha: e.target.value
        })
    }
    cambiarCorreo(e)
    {
        this.setState({
            correo: e.target.value
        })
    }
    confirmacionCorreo(e)
    {
        this.setState({
            confirmacionCorreo: e.target.value
        })
    }
    cambiarclave(e)
    {
        this.setState({
            clave: e.target.value
        }) 
    }
    enviar(e)
    {
        let valido = true
        e.preventDefault();
        if(this.state.Nombre === "")
        {
            this.setState({
            invalidNombre: true,
            mensajeNombre: "El campo es obligatorio, indica tu nombre",
        })
        let valido = false
        }
        e.preventDefault();
        if(this.state.Apellido === "")
        {
            this.setState({
            invalidApellido: true,
            mensajeApellido: "El campo es obligatorio, indica tu apellido",
        });
         valido = false
    }
    e.preventDefault();
    if(this.state.Fecha === "")
    {
        this.setState({
        invalidFecha: true,
        mensajeFecha: "El campo es obligatorio, indica tu fecha",
    });
    valido = false
    }
    e.preventDefault();
    if(this.state.correo === "")
    {
        this.setState({
        invalidCorreo: true,
        mensajeCorreo: "El campo es obligatorio, indica tu correo",
    });
    valido = false
    }
    e.preventDefault();
    if(this.state.confirmacionCorreo === "")
    {
        this.setState({
        invalidConfirmacionCorreo: true,
        mensajeConfirmacion: "El campo es obligatorio, indica tu Confirmacion de correo",
    });
    valido = false
    }
    e.preventDefault();
    if(this.state.clave === "")
    {
        this.setState({
        invalidClave: true,
        mensajeClave: "El campo es obligatorio, indica tu clave",
    });
    valido = false
    }
    console.log("se enviaron los datos" + JSON.stringify(this.state))
    }
    render()
    {
        return(
            <div>
                <Row>
                <Col xs="3"></Col>   
                <Col xs="6">
                <h1>Formulario</h1>
                <Form onSubmit={this.enviar}>
                <Label htmlFor="">Nombre</Label><br />
                <Input className="input" type="text" name="nombre" onChange={this.cambiarNombre} invalid={this.state.invalidNombre}/><br />
                <FormFeedback>{this.state.mensajeNombre}</FormFeedback>
                <Label htmlFor="">Apellido</Label><br />
                <Input className="input" type="text" name="apellido" onChange={this.cambiarApellido} invalid={this.state.invalidApellido}/><br />
                <FormFeedback>{this.state.mensajeApellido}</FormFeedback>
                <Label htmlFor="">Fecha</Label><br />
                <Col xs="4">
                <Input className="input" type="text" name="fecha" onChange={this.cambiarFecha} invalid={this.state.invalidFecha}/><br />
                <FormFeedback>{this.state.mensajeFecha}</FormFeedback>
                </Col>
                <Label htmlFor="">Correo</Label><br />
                <Input className="input" type="text" name="correo" onChange={this.cambiarCorreo} invalid={this.state.invalidCorreo}/><br />
                <FormFeedback>{this.state.mensajeCorreo}</FormFeedback>
                <Label htmlFor="">Confirmacion de Correo</Label><br />
                <Input className="input" type="text" name="ConfirmarCorreo" onChange={this.confirmacionCorreo} invalid={this.state.invalidConfirmacionCorreo}/><br />
                <FormFeedback>{this.state.mensajeConfirmacion}</FormFeedback>
                <Label for="">Clave:</Label><br />
                <Input className="input" type="text" name="clave" onChange={this.cambiarclave} invalid={this.state.invalidClave}/><br />
                <FormFeedback>{this.state.mensajeClave}</FormFeedback>
                <Label for="">Estado civil:</Label><br />
                <Label for="soltero">soltero</Label>
                <Input type="radio" name="estadoCivil" id="soltero" value="soltero"/>
                <Label for="casado">casado</Label>
                <Input type="radio" name="estadoCivil" id="casado" value="casado"/><br /><br />
                <Button className="button" >Enviar</Button>
                </Form>
                
                <h2>{`hola ${this.state.Nombre}`}</h2>
                </Col>
                </Row>
            </div>
        )
    }
}
export default Formulario
