import React from "react";

let Personaje={
   nombre: "Nombre: Homero",
   edad: "Edad: 40"
}
let Personaje1={
   nombre: "Nombre: Marge",
   edad: "Edad: 40"
}
let Personaje2={
   nombre: "Nombre: Lisa",
   edad: "Edad: 8"
}
let Personaje3={
   nombre: "Nombre: Bart",
   edad: "Edad: 10"
}
let Personaje4={
   nombre: "Nombre: Maggie",
   edad: "Edad: 2"
}
const Personajes = () =>
(
   <center>
    <div className="card" style={{width:'18rem'}}>
        <img className="homero" src="https://seeklogo.com/images/H/homero-simpsons-front-logo-2B497EF5DB-seeklogo.com.png" className="card-img-top" alt="" />
        <div className="cardbody">
        <p className="card-text">{Personaje.nombre}</p>
        <p className="card-text">{Personaje.edad}</p>
        <img class="marge" src="https://assets.stickpng.com/images/5eb95bd817f3c600044a2909.png" className="card-img-top" alt="" />
        <p className="card-text">{Personaje1.nombre}</p>
        <p className="card-text">{Personaje1.edad}</p>
        <img class="lisa" src="https://images.clipartlogo.com/files/images/48/480985/lisa-simpson-1_f.jpg" className="card-img-top" alt="" />
        <p className="card-text">{Personaje2.nombre}</p>
        <p className="card-text">{Personaje2.edad}</p>
        <img class="bart" src="https://assets.stickpng.com/images/5eb95c8017f3c600044a2910.png" className="card-img-top" alt="" />
        <p className="card-text">{Personaje3.nombre}</p>
        <p className="card-text">{Personaje3.edad}</p>
        <img class="maggie" src="https://e7.pngegg.com/pngimages/201/108/png-clipart-maggie-simpson-homer-simpson-marge-simpson-lisa-simpson-bart-simpson-bart-simpson.png" className="card-img-top" alt="" />
        <p className="card-text">{Personaje4.nombre}</p>
        <p className="card-text">{Personaje4.edad}</p>
        </div>
     
    </div>
    </center>
    
)
